import { Select } from '@chakra-ui/react';
import styled from '@emotion/styled';
import { Link, useNavigate } from 'react-router-dom';

import { URLS } from '@/api';
import { Container } from '@/components/common/layouts/Container';
import { useAuth } from '@/provider/Auth';
import { getDynamicPath, RouterPath } from '@/routes/path';
import { apiSessionStorage, authSessionStorage } from '@/utils/storage';

export const Header = () => {
  const navigate = useNavigate();
  const authInfo = useAuth();

  const handleLogin = () => {
    navigate(getDynamicPath.login());
  };

  const handleApiChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const url = e.target.value;
    apiSessionStorage.set(url);
    authSessionStorage.set('');
    window.location.reload();
  };

  return (
    <Wrapper>
      <Container flexDirection="row" alignItems="center" justifyContent="space-between">
        <Link to={RouterPath.home}>
          <Logo
            src="https://gift-s.kakaocdn.net/dn/gift/images/m640/pc_gift_logo.png"
            alt="카카오 선물하기 로고"
          />
        </Link>
        <RightWrapper>
          <Select
            placeholder="벡엔드 API"
            value={apiSessionStorage.get()}
            onChange={handleApiChange}
          >
            {URLS.map((info) => (
              <option key={info.id} value={info.url}>
                {info.name}
              </option>
            ))}
          </Select>
          {authInfo ? (
            <LinkButton onClick={() => navigate(RouterPath.myAccount)}>내 계정</LinkButton>
          ) : (
            <LinkButton onClick={handleLogin}>로그인</LinkButton>
          )}
        </RightWrapper>
      </Container>
    </Wrapper>
  );
};

export const HEADER_HEIGHT = '54px';

export const Wrapper = styled.header`
  position: fixed;
  z-index: 9999;
  width: 100%;
  max-width: 100vw;
  height: ${HEADER_HEIGHT};
  background-color: #fff;
  padding: 0 16px;
`;

const Logo = styled.img`
  height: ${HEADER_HEIGHT};
`;
const RightWrapper = styled.div`
  display: flex;
  gap: 16px;
`;

const LinkButton = styled.p`
  align-items: center;
  font-size: 14px;
  color: #000;
  text-decoration: none;
  cursor: pointer;
  min-width: 60px;
  display: flex;
  justify-content: center;
`;
